import React, { Component } from "react";
import TokenTimelock from "./contracts/TokenTimelock.json";
import Web3Comp from "./components/Web3tools";
import {LockTokenForm} from "./components/TokenLocks";
import { BrowserRouter, Routes, Route, Link, NavLink } from 'react-router-dom';
import Home from './routes/Home';
import Roadmap from './routes/Roadmap';
import Contact from './routes/Contact';
import MyLocks from './routes/MyLocks';
import LockDetails from "./components/LockDetails";


import "./App.css";
import {dataService} from './components/user-context';

class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
      web3: null,
      search: ''
    };
  }

  handleSearch(){

    let search = this.state.search;
    let errors = false;

    if (!search) {
      errors = 'Required';
    } else if (
      !/^0x[a-fA-F0-9]{40}$/.test(search)
    ) {
      errors = 'Invalid beneficiary address';
    }

    if(errors){
      alert("Invalid Address");

    }else{
      window.location = "/OracleLocker/lock-details/" + this.state.search
    }

    

  }


  render(){
     return (
      <BrowserRouter basename="/OracleLocker" >
       <div className="component-app">

            <nav class="navbar navbar-expand-lg navbar-dark bg-dark" >
              <NavLink to={'/'} className={({ isActive }) => (isActive ? 'navbar-brand btn active' : 'navbar-brand btn inactive')} >  
              <img src="favicon.png" width="30" height="30" class="d-inline-block align-top" alt=""/>
              Oracle Locker
              </NavLink>
              
              <ul className="navbar-nav mr-auto ">
                <li class="nav-item" >
                  <NavLink to={'/mylocks'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')} > My Locks </NavLink>
                </li>
                <li class="nav-item" >
                  <NavLink to={'/locktoken'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')}>Lock Tokens</NavLink>
                </li>
                <li class="nav-item" >
                  <NavLink to={'/roadmap'} className={({ isActive }) => (isActive ? 'nav-link btn active' : 'nav-link btn inactive')}>Roadmap</NavLink>
                </li>
              </ul>

              <form class="form-inline" >
                  <input class="form-control mr-sm-2 " type="search" placeholder="0x... address" aria-label="Search" value={this.state.search}
                    onChange={
                      (e)=> this.setState({search: e.target.value})
                    } 
                  />
                  <button class="btn btn-outline-success my-2 my-sm-0 btn-sm" type="button" onClick={this.handleSearch.bind(this)} >Search By Lock Contract</button>
              </form>

              <Web3Comp></Web3Comp>

            </nav>


            <div class="modal fade bd-example-modal-sm" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" id="mySmallModalLabel" aria-hidden="true">
              <div class="modal-dialog modal-sm">
                <div class="modal-content">
                  <div class="row text-center">
                      <div class="col-md-12">
                          <span id="status"></span>
                      </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="container">

              <Routes>
                <Route path="/" element={<Home />}></Route>
                <Route path="/mylocks" element={<MyLocks web3={this.state.web3} currentAccount={this.state.currentAccount} />}></Route>
                <Route path="/locktoken" element={<LockTokenForm web3={this.state.web3} currentAccount={this.state.currentAccount} />}></Route>

                <Route path="/roadmap" element={<Roadmap />}></Route>

                <Route path="/lock-details/:contract" element={<LockDetails web3={this.state.web3} currentAccount={this.state.currentAccount} />} >
                </Route>
                
              </Routes>
              
            </div>
      
        
      </div>
      </BrowserRouter>
     );
  }

  componentDidMount() {

    dataService.getData().subscribe(message => {
      
      this.setState({ web3: message.value.web3, currentAccount: message.value.currentAccount});

    });
  }

}

export default App;
